import ComponentNumberPad from './Components/Subtypes/Input/ComponentNumberPad.js'
import ComponentAddition from './Components/Subtypes/Arithmetic/ComponentAddition.js';
import ComponentNum2Text from './Components/Subtypes/Converters/ComponentNum2Text.js';
import ComponentTextToSpeech from './Components/Subtypes/Output/ComponentTextToSpeech.js';
import Graph from './Graph.js'
import AssemblyView from './View/AssemblyView.js';
import React from 'react';
import ReactDOM from 'react-dom';
import './style.css';

var graph = new Graph();
var numberPad = new ComponentNumberPad();
var adder = new ComponentAddition();
var num2Text = new ComponentNum2Text();
var toSpeech = new ComponentTextToSpeech();

graph.addComponent(numberPad);
graph.addComponent(adder);
graph.addComponent(num2Text);
graph.addComponent(toSpeech);

adder.getPort("A").setValue(3);

graph.linkPorts(numberPad.getPort("Number Entered"), adder.getPort("B"));
graph.linkPorts(adder.getPort("Result"), num2Text.getPort("Input"));
graph.linkPorts(num2Text.getPort("Output"), toSpeech.getPort("Text"));

graph.linkActions(numberPad.getAction("On Entered"), adder.getAction("Compute"));
graph.linkActions(adder.getAction("On Compute"), num2Text.getAction("Convert"));
graph.linkActions(num2Text.getAction("On Converted"), toSpeech.getAction("To Speech"));

ReactDOM.render(<AssemblyView graph={graph} />, document.getElementById("root"));


