export var ComponentPortTypes = {
    In: 0,
    Out: 1
}

export var ComponentValueTypes = {
    Number: 0,
    String: 1,
    Reference: 2,
    Bool: 3,
    Any: 4
}

export function getValueTypeName(type) {
    if (type === ComponentValueTypes.Number) {
        return "NUM";
    } else if (type === ComponentValueTypes.String) {
        return "TEXT";
    } else if (type === ComponentValueTypes.Reference) {
        return "REF";
    } else if (type === ComponentValueTypes.Bool) {
        return "BOOL";
    } else if (type === ComponentValueTypes.Any) {
        return "ANY";
    } else {
        console.log("Unknown Value Type (" + this.type + ")");
    }
}


