import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes.js';

class ComponentTextToSpeech extends Component {
    constructor() {
        super();

        this.componentName = "text-to-speech circuit";
        this.createPort("Text", ComponentPortTypes.In, ComponentValueTypes.String);
        this.createAction("To Speech", this.toSpeech);
    }

    toSpeech(component, action) {
        console.log(component.getPort("Text").value);
    }
}

export default ComponentTextToSpeech;


