import React from 'react';
import SkyLight from 'react-skylight';

class AssemblySettingsView extends React.Component {
    constructor(props) {
        super(props);

        this.assemblyTypeSelection = null;
    }

    render() {
        return (
            <SkyLight ref={ref => this.skylight = ref} title="Assembly Settings">
                <span>
                    Assembly Type:
                    <select ref={ref => this.assemblyTypeSelection = ref}>
                        <option value="type-a electronic assembly">Electronic Assembly</option>
                        <option value="type-b electronic assembly">Calculator Assembly</option>
                        <option value="type-c electronic assembly">Clamshell Assembly</option>
                        <option value="type-d electronic assembly">Simple Assembly</option>

                        <option value="type-a electronic mechanism">Electronic Mechanism</option>
                        <option value="type-b electronic mechanism">Boxy Mechanism</option>
                        <option value="type-c electronic mechanism">Clamshell Mechanism</option>
                        <option value="type-d electronic mechanism">Medical Apparatus Mechanism</option>

                        <option value="type-a electronic machine">Electronic Machine</option>
                        <option value="type-b electronic machine">Oscilloscope Machine</option>
                        <option value="type-c electronic machine">Computer Terminal Machine</option>
                        <option value="type-d electronic machine">Robotic Arm Machine</option>

                        <option value="type-a electronic drone">Electronic Drone</option>
                        <option value="type-b electronic drone">Armed and Dangerous Drone</option>
                    </select>
                </span>
                <br />
                <span>
                    Name:
                    <input ref={ref => this.assemblyNameInput = ref} />
                </span>
                <br />
                <span>
                    Opened:
                    <input ref={ref => this.assemblyOpenedCheckbox = ref} type="checkbox" />
                </span>
                <br />
                <button onClick={() => this.save()}>Save</button>
            </SkyLight>
        )
    }

    show() {
        this.assemblyNameInput.value = this.props.graph.assembly.name;
        this.assemblyOpenedCheckbox.checked = this.props.graph.assembly.opened;
        for (let i=0; i<this.assemblyTypeSelection.children.length; i++) {
            if (this.assemblyTypeSelection.children[i].value === this.props.graph.assembly.type) {
                this.assemblyTypeSelection.selectedIndex = i;
                break;
            }
        }

        this.skylight.show();
    }

    save() {
        this.props.graph.assembly.type = this.assemblyTypeSelection.value;
        this.props.graph.assembly.name = this.assemblyNameInput.value;
        this.props.graph.assembly.opened = this.assemblyOpenedCheckbox.checked;
        this.skylight.setState({isVisible:false});
    }
}

export default AssemblySettingsView;


