import ComponentPortLink from './Components/ComponentPortLink.js';
import ComponentActionLink from './Components/ComponentActionLink.js';
import {ComponentPortTypes, ComponentValueTypes} from './Components/ComponentTypes.js';

import {ComponentNumberPad, ComponentToggleButton} from './Components/Subtypes/Input.js';
import {ComponentAddition} from './Components/Subtypes/Arithmetic.js';
import {ComponentNum2Text} from './Components/Subtypes/Converters.js';
import {ComponentTextToSpeech, ComponentSmallScreen} from './Components/Subtypes/Output.js';
import {ComponentSmoke} from './Components/Subtypes/Reagents.js';
import {ComponentFastTicker} from './Components/Subtypes/Time.js';
import {ComponentTeslaPowerRelay} from './Components/Subtypes/Passive.js';
import ComponentMediumScreen from './Components/Subtypes/Output/ComponentMediumScreen';

class Graph {
    constructor() {
        this.components = [];
        this.portLinks = [];
        this.actionLinks = [];
        this.newComponentCallbacks = [];
        this.removedComponentCallbacks = [];
        this.newPortLinkCallbacks = [];
        this.removedPortLinkCallbacks = [];
        this.newActionLinkCallbacks = [];
        this.removedActionLinkCallbacks = [];
        this.assembly = {
            type: "electronic assembly",
            name: "Device",
            opened: true
        };
    }

    addComponent(component) {
        this.components.push(component);

        var selfGraph = this;
        for (let i=0; i<component.actions.length; i++) {
            component.actions[i].addActCallback(function(component, action) {
                selfGraph.onActionCalled(component, action);
            });
        }
        
        for (let i=0; i<this.newComponentCallbacks.length; i++) {
            this.newComponentCallbacks[i](component);
        }
    }

    removeComponent(component) {
        for (let i=0; i<this.portLinks.length; i++) {
            let portLink = this.portLinks[i];
            
            if (portLink.outPort.component === component || portLink.inPort.component === component) {
                this.portLinks.splice(i--, 1);
                for (let b=0; b<this.removedPortLinkCallbacks.length; b++) {
                    this.removedPortLinkCallbacks[b](portLink);
                }
            }
        }

        for (let i=0; i<this.actionLinks.length; i++) {
            let actionLink = this.actionLinks[i];
            
            if (actionLink.action1.component === component || actionLink.action2.component === component) {
                this.actionLinks.splice(i--, 1);
                for (let b=0; b<this.removedActionLinkCallbacks.length; b++) {
                    this.removedActionLinkCallbacks[b](actionLink);
                }
            }
        }

        this.components.splice(this.components.indexOf(component), 1);
        for (let i=0; i<this.removedComponentCallbacks.length; i++) {
            this.removedComponentCallbacks[i](component);
        }
    }
    
    addNewComponentCallback(callback) {
        this.newComponentCallbacks.push(callback);
    }

    addRemovedComponentCallback(callback) {
        this.removedComponentCallbacks.push(callback);
    }

    addNewPortLinkCallback(callback) {
        this.newPortLinkCallbacks.push(callback);
    }

    addRemovedPortLinkCallback(callback) {
        this.removedPortLinkCallbacks.push(callback);
    }

    addNewActionLinkCallback(callback) {
        this.newActionLinkCallbacks.push(callback);
    }

    addRemovedActionLinkCallback(callback) {
        this.removedActionLinkCallbacks.push(callback);
    }

    linkPorts(outPort, inPort) {
        var link = new ComponentPortLink(outPort, inPort);

        if (link.valid) {
            this.portLinks.push(link);
        }

        for (let i=0; i<this.newPortLinkCallbacks.length; i++) {
            this.newPortLinkCallbacks[i](link);
        }
    }

    linkActions(action1, action2) {
        let link = new ComponentActionLink(action1, action2);

        this.actionLinks.push(new ComponentActionLink(action1, action2));
        for (let i=0; i<this.newActionLinkCallbacks.length; i++) {
            this.newActionLinkCallbacks[i](link);
        }
    }

    unlinkPorts(portLink) {
        for (let i=0; i<this.portLinks.length; i++) {
            let link = this.portLinks[i];

            if (link === portLink) {
                this.portLinks.splice(i, 1);
                for (let b=0; b<this.removedPortLinkCallbacks.length; b++) {
                    this.removedPortLinkCallbacks[b](portLink);
                }

                break;
            }
        }
    }

    unlinkActions(actionLink) {
        for (let i=0; i<this.actionLinks.length; i++) {
            let link = this.actionLinks[i];

            if (link === actionLink) {
                this.actionLinks.splice(i, 1);
                for (let b=0; b<this.removedActionLinkCallbacks.length; b++) {
                    this.removedActionLinkCallbacks[b](link);
                }

                break;
            }
        }
    }

    onActionCalled(component, action) {
        for (var i=0; i<this.actionLinks.length; i++) {
            var actionLink = this.actionLinks[i];

            if (actionLink.action1 === action) {
                actionLink.action2.act();
            }
        }
    }

    exportToJSON() {
        var json = {};

        json.assembly = {};
        json.assembly.type = this.assembly.type;
        json.assembly.name = this.assembly.name;
        json.assembly.opened = this.assembly.opened;
        
        json.components = [];
        for (let i=0; i<this.components.length; i++) {
            let component = this.components[i];
            let jsonEntry = {};

            jsonEntry.type = component.componentName;
            //TODO: Circuit Name

            let inputIndex = 0;
            let inputs = [];
            for (let b=0; b<component.ports.length; b++) {
                let port = component.ports[b];

                if (port.type === ComponentPortTypes.In) {
                    inputIndex++;

                    if (!port.isDefault()) {
                        inputs.push([inputIndex, 0, port.getJSONValue()]);
                    }
                }
            }

            if (inputs.length !== 0) {
                jsonEntry.inputs = inputs;
            }

            json.components.push(jsonEntry);
        }

        json.wires = [];
        for (let i=0; i<this.portLinks.length; i++) {
            let portLink = this.portLinks[i];
            let outPortComponentID = -1;
            let inPortComponentID = -1;
            let outPortID = -1;
            let inPortID = -1;
            let jsonEntry = [];

            for (let b=0; b<this.components.length; b++) {
                if (this.components[b] === portLink.outPort.component) {
                    outPortComponentID = b;
                    break;
                }
            }

            for (let b=0; b<this.components.length; b++) {
                if (this.components[b] === portLink.inPort.component) {
                    inPortComponentID = b;
                    break;
                }
            }

            for (let b=0; b<portLink.outPort.component.ports.length; b++) {
                let port = portLink.outPort.component.ports[b];

                if (port.type === ComponentPortTypes.Out) {
                    outPortID++;
                    if (port === portLink.outPort) {
                        break;
                    }
                }
            }

            for (let b=0; b<portLink.inPort.component.ports.length; b++) {
                let port = portLink.inPort.component.ports[b];
                
                if (port.type === ComponentPortTypes.In) {
                    inPortID++;
                    if (port === portLink.inPort) {
                        break;
                    }
                }
            }

            jsonEntry.push([outPortComponentID + 1, "O", outPortID + 1]);
            jsonEntry.push([inPortComponentID + 1, "I", inPortID + 1]);
            json.wires.push(jsonEntry);
        }

        for (let i=0; i<this.actionLinks.length; i++) {
            let actionLink = this.actionLinks[i];
            let action1ComponentID = -1;
            let action2ComponentID = -1;
            let action1ID = -1;
            let action2ID = -1;
            let jsonEntry = [];

            for (let b=0; b<this.components.length; b++) {
                if (this.components[b] === actionLink.action1.component) {
                    action1ComponentID = b;
                    break;
                }
            }

            for (let b=0; b<this.components.length; b++) {
                if (this.components[b] === actionLink.action2.component) {
                    action2ComponentID = b;
                    break;
                }
            }

            for (let b=0; b<actionLink.action1.component.actions.length; b++) {
                if (actionLink.action1.component.actions[b] === actionLink.action1) {
                    action1ID = b;
                    break;
                }
            }

            for (let b=0; b<actionLink.action2.component.actions.length; b++) {
                if (actionLink.action2.component.actions[b] === actionLink.action2) {
                    action2ID = b;
                    break;
                }
            }

            jsonEntry.push([action1ComponentID + 1, "A", action1ID + 1]);
            jsonEntry.push([action2ComponentID + 1, "A", action2ID + 1]);
            json.wires.push(jsonEntry);
        }

        return JSON.stringify(json).replace(/\\/g, '');
    }

    importFromJSON(jsonString) {
        let json = JSON.parse(jsonString);
        if (json.assembly == null || json.components == null || json.wires == null) {
            alert("Invalid import: Missing something.");
            return;
        }

        //Reset
        while (this.components.length > 0) {
            this.removeComponent(this.components[0]);
        }

        this.assembly.type = json.assembly.type;
        this.assembly.name = json.assembly.name;
        this.assembly.opened = json.assembly.opened ? json.assembly.opened : true;

        let types = {"number pad": ComponentNumberPad, "addition circuit": ComponentAddition, "number to string": ComponentNum2Text, "text-to-speech circuit": ComponentTextToSpeech, "smoke generator": ComponentSmoke, "fast ticker": ComponentFastTicker, "tesla power relay": ComponentTeslaPowerRelay, "toggle button": ComponentToggleButton, "small screen": ComponentSmallScreen, "screen": ComponentMediumScreen};
        for (let i=0; i<json.components.length; i++) {
            let component = json.components[i];

            if (types[component.type] == null) {
                alert("Unknown Circuit Type (" + component.type + ")!");
                return;
            } else {
                let createdComponent = new types[component.type]();
                this.addComponent(createdComponent);

                if (component.inputs != null) {
                    for (let b=0; b<component.inputs.length; b++) {
                        let inputEntry = component.inputs[b];
                        let port = createdComponent.ports[inputEntry[0]-1];
                        
                        if (port.valueType === ComponentValueTypes.Bool) {
                            port.setValue(inputEntry[2] === 1);
                        } else {
                            port.setValue(inputEntry[2]);
                        }
                    }
                }
            }
        }

        for (let i=0; i<json.wires.length; i++) {
            let linkEntry = json.wires[i];
            let port1 = linkEntry[0];
            let port2 = linkEntry[1];

            if (port1[1] === "O" && port2[1] === "I") {
                let port1Component = this.components[port1[0]-1];
                let port1PortIndex = 0;
                let port2Component = this.components[port2[0]-1];
                let port2PortIndex = 0;

                for (let b=0; b<port1Component.ports.length; b++) {
                    let port = port1Component.ports[b];

                    if (port.type === ComponentPortTypes.Out) {
                        if (port1[2] === ++port1PortIndex) {
                            port1PortIndex = b;
                            break;
                        }
                    }
                }

                for (let b=0; b<port2Component.ports.length; b++) {
                    let port = port2Component.ports[b];

                    if (port.type === ComponentPortTypes.In) {
                        if (port2[2] === ++port2PortIndex) {
                            port2PortIndex = b;
                            break;
                        }
                    }
                }

                this.linkPorts(port1Component.ports[port1PortIndex], port2Component.ports[port2PortIndex]);
            } else if (port1[1] === "I" && port2[1] === "O") {
                let port1Component = this.components[port1[0]-1];
                let port1PortIndex = 0;
                let port2Component = this.components[port2[0]-1];
                let port2PortIndex = 0;

                for (let b=0; b<port1Component.ports.length; b++) {
                    let port = port1Component.ports[b];

                    if (port.type === ComponentPortTypes.In) {
                        if (port1[2] === ++port1PortIndex) {
                            port1PortIndex = b;
                            break;
                        }
                    }
                }

                for (let b=0; b<port2Component.ports.length; b++) {
                    let port = port2Component.ports[b];

                    if (port.type === ComponentPortTypes.Out) {
                        if (port2[2] === ++port2PortIndex) {
                            port2PortIndex = b;
                            break;
                        }
                    }
                }

                this.linkPorts(port2Component.ports[port2PortIndex], port1Component.ports[port1PortIndex]);
            } else if (port1[1] === "A" && port2[1] === "A") {
                this.linkActions(this.components[port1[0]-1].actions[port1[2]-1], this.components[port2[0]-1].actions[port2[2]-1]);
            } else {
                alert("Invalid import: Incorrect wire data.");
                return;
            }
        }
    }
}

export default Graph;


