import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentFastTicker extends Component {
    constructor() {
        super();

        this.componentName = "fast ticker";
        this.createPort("Enable Ticking", ComponentPortTypes.In, ComponentValueTypes.Bool);
        this.createAction("Outgoing Pulse", this.outgoingPulse);
    }

    outgoingPulse(component, action) {
        
    }
}

export default ComponentFastTicker;


