import React from 'react';
import SkyLight from 'react-skylight';

class ExportAssemblyView extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            json: this.props.json,
            isCopied: false
        }
    }

    render() {
        return (
            <SkyLight ref={ref => this.skylight = ref} afterClose={() => this.onClose()} title="Export Assembly">
                <textarea readOnly className="exportTextArea" value={this.state.json} ref={ref => this.textarea = ref} />
                <button onClick={() => this.copyToClipboard()}>Copy to clipboard</button>{this.state.isCopied && <span>Copied!</span>}
            </SkyLight>
        );
    }

    show() {
        this.skylight.show();
    }

    onClose() {
        this.setState({
            isCopied: false
        })
    }

    copyToClipboard() {
        this.textarea.select();
        document.execCommand("copy");

        this.setState({
            isCopied: true
        })
    }
}

export default ExportAssemblyView;


