import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentToggleButton extends Component {
    constructor() {
        super();

        this.componentName = "toggle button";
        this.createPort("On", ComponentPortTypes.Out, ComponentValueTypes.Bool);
        this.createAction("On Toggle", this.onToggle);
    }

    onToggle(component, action) {
        
    }
}

export default ComponentToggleButton;


