import ComponentPort from './ComponentPort.js';
import ComponentAction from './ComponentAction.js';
import {ComponentPortTypes} from './ComponentTypes.js';

class Component {
    constructor() {
        this.ports = [];
        this.actions = [];
        this.componentName = "component";
    }

    createPort(name, type, valueType) {
        var port = new ComponentPort(name, type, valueType, this);
        
        this.ports.push(port);
        return port;
    }

    createAction(name, actCallback) {
        this.actions.push(new ComponentAction(name, this, actCallback));
    }

    getPort(portName) {
        for (var i=0; i<this.ports.length; i++) {
            var port = this.ports[i];

            if (port.name === portName) {
                return port;
            }
        }

        console.log("Invalid port (" + portName + ")");
    }

    getInputs() {
        let inputs = [];

        for (let i=0; i<this.ports.length; i++) {
            if (this.ports[i].type === ComponentPortTypes.In) {
                inputs.push(this.ports[i]);
            }
        }

        return inputs;
    }

    getOutputs() {
        let outputs = [];

        for (let i=0; i<this.ports.length; i++) {
            if (this.ports[i].type === ComponentPortTypes.Out) {
                outputs.push(this.ports[i]);
            }
        }

        return outputs;
    }

    getAction(actionName) {
        for (var i=0; i<this.actions.length; i++) {
            var action = this.actions[i];

            if (action.name === actionName) {
                return action;
            }
        }

        console.log("Invalid action (" + actionName + ")");
    }

    runAction(actionName) {
        var action = this.getAction(actionName);

        if (action != null) {
            action.act();
        }
    }
}

export default Component;


