import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes.js';

class ComponentNumberPad extends Component {
    constructor() {
        super();

        this.componentName = "number pad";
        this.createPort("Number Entered", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createAction("On Entered", this.onEntered);
    }

    onEntered(component, action) {
        
    }
}

export default ComponentNumberPad;


