import React from 'react';
import SkyLight from 'react-skylight';

import {ComponentButton, ComponentNumberPad, ComponentToggleButton} from '../Components/Subtypes/Input.js';
import {ComponentAddition, ComponentSubtraction, ComponentMultiplication, ComponentDivision, ComponentExponent, ComponentSign, ComponentAbsolute} from '../Components/Subtypes/Arithmetic.js';
import {ComponentNum2Text} from '../Components/Subtypes/Converters.js';
import {ComponentTextToSpeech, ComponentSmallScreen, ComponentMediumScreen} from '../Components/Subtypes/Output.js';
import {ComponentTeslaPowerRelay} from '../Components/Subtypes/Passive.js';
import {ComponentSmoke} from '../Components/Subtypes/Reagents.js';
import {ComponentFastTicker} from '../Components/Subtypes/Time.js';

class CreateComponentView extends React.Component {
    constructor(props) {
        super(props);
        this.sections = [];
        this.createSection("Input", [[ComponentButton, "Button"], [ComponentNumberPad, "Number Pad"], [ComponentToggleButton, "Toggle Button"]]);
        this.createSection("Arithmetic", [[ComponentAddition, "Addition"], [ComponentSubtraction, "Subtraction"], [ComponentMultiplication, "Multiplication"], [ComponentDivision, "Division"], [ComponentExponent, "Exponent"], [ComponentSign, "Sign"], [ComponentAbsolute, "Absolute"]]);
        this.createSection("Converters", [[ComponentNum2Text, "Number to Text"]]);
        this.createSection("Output", [[ComponentTextToSpeech, "Text to Speech"], [ComponentSmallScreen, "Small Screen"], [ComponentMediumScreen, "Medium Screen"]]);
        this.createSection("Passive", [[ComponentTeslaPowerRelay, "Tesla Power Relay"]]);
        this.createSection("Reagents", [[ComponentSmoke, "Smoke"]]);
        this.createSection("Time", [[ComponentFastTicker, "Fast Ticker"]]);

        this.state = {currentSection: this.sections[0]};
    }

    render() {
        let sectionSwitchers = [];
        let sectionComponents = [];

        for (let i=0; i<this.sections.length; i++) {
            let section = this.sections[i];

            sectionSwitchers.push(<button key={Math.random()} onClick={() => this.setSection(section)}>{section.name}</button>);

            if (section === this.state.currentSection) {
                for (let b=0; b<section.components.length; b++) {
                    let component = section.components[b];

                    sectionComponents.push(<button key={Math.random()} onClick={() => this.createComponent(component[0])}>{component[1]}</button>);
                }
            }
        }

        return (
            <SkyLight ref={ref => this.skylight = ref} title="Create Circuit">
                {sectionSwitchers}
                <hr />
                {sectionComponents}
            </SkyLight>
        );
    }

    show() {
        this.skylight.show();
    }

    createComponent(component) {
        this.props.graph.addComponent(new component());
        this.skylight.setState({isVisible: false});
    }

    createSection(name, components) {
        this.sections.push({name: name, components:components});
    }

    setSection(section) {
        this.setState({currentSection: section});
    }
}

export default CreateComponentView;


