import React from 'react';
import SkyLight from 'react-skylight';

class ImportAssemblyView extends React.Component {
    render() {
        return (
            <SkyLight ref={ref => this.skylight = ref} title="Import Assembly">
                <textarea className="importTextArea" ref={ref => this.textarea = ref} />
                <button onClick={() => this.import()}>Import</button>
            </SkyLight>
        );
    }

    show() {
        this.textarea.select();
        this.skylight.show();
    }

    import() {
        this.props.graph.importFromJSON(this.textarea.value);
        this.skylight.setState({isVisible: false});
    }
}

export default ImportAssemblyView;


