import React from 'react';
import ComponentView from './ComponentView.js';
import AssemblySettingsView from './AssemblySettingsView.js';
import CreateComponentView from './CreateComponentView.js';
import ExportAssemblyView from './ExportAssemblyView.js';
import ImportAssemblyView from './ImportAssemblyView.js';
import {ComponentPortTypes} from '../Components/ComponentTypes.js';

class AssemblyView extends React.Component {
    constructor(props) {
        super(props);

        let graph = props.graph;
        graph.addNewComponentCallback(component => this.componentListChanged());
        graph.addRemovedComponentCallback(component => this.componentListChanged());

        this.state = {
            components: graph.components,
            text: null,
            linkingPort: null,
            linkingAction: null,
        }
    }
    
    render() {
        return (
            <div className="assemblyView">
                <button onClick={() => this.assemblySettingsClicked()}>Assembly Settings</button>  
                <button onClick={() => this.createComponentClicked()}>Create Circuit</button>
                <button onClick={() => this.exportAssemblyClicked()}>Export</button>
                <button onClick={() => this.importAssemblyClicked()}>Import</button>
                {this.state.text != null && <span>{this.state.text}</span>}
                <hr />
                {this.state.components.map(value => {
                    return <ComponentView key={value.componentName+Math.random()} component={value} graph={this.props.graph} portLinkClicked={port => this.portLinkClicked(port)} actionLinkClicked={action => this.actionLinkClicked(action)} />
                })}
                <AssemblySettingsView ref={ref => this.assemblySettings = ref} graph={this.props.graph} />
                <CreateComponentView ref={ref => this.createComponentView = ref} graph={this.props.graph} />
                <ExportAssemblyView ref={ref => this.exportAssemblyView = ref} json={this.props.graph.exportToJSON()} />
                <ImportAssemblyView ref={ref => this.importAssemblyView = ref} graph={this.props.graph} />
            </div>
        );
    }

    assemblySettingsClicked() {
        this.assemblySettings.show();
    }

    componentListChanged(component) {
        this.setState({
            components: this.props.graph.components
        });
    }

    createComponentClicked() {
        this.createComponentView.show();
    }

    exportAssemblyClicked() {
        this.exportAssemblyView.setState({json: this.props.graph.exportToJSON()})
        this.exportAssemblyView.show();
    }

    importAssemblyClicked() {
        this.importAssemblyView.show();
    }

    portLinkClicked(port) {
        if (this.state.linkingPort === null) {
            this.setState({
                text: (<span>Linking <b>{port.name} @ {port.component.componentName}</b>.</span>),
                linkingPort: port
            })
        } else {
            if (this.state.linkingPort.type === port.type) {
                this.setState({
                    text: (<span>Cannot link <b>{this.state.linkingPort.name} @ {this.state.linkingPort.component.componentName}</b> with <b>{port.name} @ {port.component.componentName}</b>.</span>),
                    linkingPort: null
                });

                return;
            }
            
            if (this.state.linkingPort.type === ComponentPortTypes.Out) {
                this.props.graph.linkPorts(this.state.linkingPort, port);
            } else {
                this.props.graph.linkPorts(port, this.state.linkingPort);
            }

            this.setState({
                text: null,
                linkingPort: null
            });
        }
    }

    actionLinkClicked(action) {
        if (this.state.linkingAction === null) {
            this.setState({
                text: (<span>Linking <b>{action.name} @ {action.component.componentName}</b>.</span>),
                linkingAction: action
            })
        } else {
            if (this.state.linkingAction === action) {
                this.setState({
                    text: (<span>Cannot link an action to itself.</span>),
                    linkingAction: null
                });

                return;
            } else if (this.state.linkingAction.component === action.component) {
                this.setState({
                    text: (<span>Cannot link <b>{this.state.linkingAction.name} @ {this.state.linkingAction.component.componentName}</b> with <b>{action.name} @ {action.component.componentName}</b>.</span>),
                    linkingAction: null
                });

                return;
            }
            
            this.props.graph.linkActions(this.state.linkingAction, action);

            this.setState({
                text: null,
                linkingAction: null
            });
        }
    }
}

export default AssemblyView;


