import React from 'react';
import SkyLight from 'react-skylight';
import {ComponentValueTypes, getValueTypeName} from '../Components/ComponentTypes.js';

class ModifyPortValueView extends React.Component {
    constructor(props) {
        super(props);
        this.input = [];
        this.input.view = null;
        this.input.onSave = null;
        this.input.onOpen = null;
    }

    render() {
        this.input = this.getInput(this.props.port.valueType);

        return (
            <SkyLight ref={ref => this.skylight = ref} title="Modify Value">
                <p>Type: {getValueTypeName(this.props.port.valueType)}</p>
                {this.input.view}
                <br /><br />
                <button onClick={() => this.saveButtonClicked()}>Save</button>
            </SkyLight>
        );
    }

    show() {
        this.input.onOpen(this.props.port);
        this.skylight.show();
    }

    saveButtonClicked() {
        this.input.onSave(this.props.port);
        this.skylight.setState({isVisible: false});
    }

    checkEnter(e) {
        if (e.charCode === 13) {
            this.saveButtonClicked();
        }
    }

    getInput(type) {
        let input = [];

        if (type === ComponentValueTypes.Number) {
            let inputBox = null;

            input.view = <input type="number" ref={ref => inputBox = ref} onKeyPress={e => this.checkEnter(e)} />
            input.onOpen = function(port) {
                inputBox.value = port.value;
                inputBox.select();
            }
            input.onSave = function(port) {
                let value = parseFloat(inputBox.value);

                if (isNaN(value)) {
                    port.setValue(null);
                } else {
                    port.setValue(value);
                }
            }
        } else if (type === ComponentValueTypes.String) {
            let inputBox = null;

            input.view = <input ref={ref => inputBox = ref} onKeyPress={e => this.checkEnter(e)} />
            input.onOpen = function(port) {
                inputBox.value = port.value;
                inputBox.select();
            }
            input.onSave = function(port) {
                port.setValue(inputBox.value);
            }
        } else if (type === ComponentValueTypes.Reference) {
            input.view = <span>References cannot be edited or exported.</span>
            input.onOpen = function(port) {

            }
            input.onSave = function(port) {

            }
        } else if (type === ComponentValueTypes.Bool) {
            let dropdown = null;

            input.view = <select ref={ref => dropdown = ref}><option value={true}>true</option><option value={false}>false</option></select>
            input.onOpen = function(port) {
                dropdown.value = port.value;
            }
            input.onSave = function(port) {
                port.setValue(dropdown.value);
            }
        } else if (type === ComponentValueTypes.Any) {
            let typeSelection = null;
            let typeSelectionOptions = [];
            let typeInputs = [];
            let currentTypeInput = 0;

            for (let i=0; i<4; i++) {
                let input = this.getInput(i);
                let inputEntry = {
                    onOpen: input.onOpen,
                    onSave: input.onSave,
                }

                inputEntry.view = (
                    <div key={Math.random()} ref={ref => inputEntry.ref = ref}>
                        {input.view}
                    </div>
                )

                typeInputs.push(inputEntry);
                typeSelectionOptions.push(<option key={Math.random()} value={i}>{getValueTypeName(i)}</option>)
            }

            input.view = (
                <div>
                    <select ref={ref => typeSelection = ref} onChange={() => {typeInputs[currentTypeInput].ref.style.display="none";currentTypeInput=typeSelection.value;typeInputs[currentTypeInput].ref.style.display="inline";}}>{typeSelectionOptions}</select>
                    <br />
                    {typeInputs.map(value => {return value.view})}
                </div>
            )
            input.onOpen = function(port) {
                for (let i=0; i<typeInputs.length; i++) {
                    if (i !== currentTypeInput) {
                        typeInputs[i].ref.style.display = "none";
                    } else {
                        typeInputs[i].ref.style.display = "inline";
                    }

                    typeInputs[i].onOpen(port);
                }
            }
            input.onSave = function(port) {
                typeInputs[currentTypeInput].onSave(port);
            }
        } else {
            console.log("Modify Port Value: Unknown port type (" + type + ")");
        }

        return input;
    }
}

export default ModifyPortValueView;


