import {ComponentValueTypes} from './ComponentTypes.js';

class ComponentPort {
    constructor(name, type, valueType, component) {
        this.name = name;
        this.type = type;
        this.valueType = valueType;
        this.component = component;
        this.valueChangedListeners = [];

        if (valueType === ComponentValueTypes.Number) {
            this.value = null;
        } else if (valueType === ComponentValueTypes.String) {
            this.value = "";
        } else if (valueType === ComponentValueTypes.Reference) {
            this.value = null;
        } else if (valueType === ComponentValueTypes.Bool) {
            this.value = false;
        } else if (valueType === ComponentValueTypes.Any) {
            this.value = null;
        } else {
            console.log("Unknown Port Value Type (" + this.type + ")");
        }
    }

    setValue(value) {
        this.value = value;
        
        for (let i=0; i<this.valueChangedListeners.length; i++) {
            this.valueChangedListeners[i](this, value);
        }
    }

    addValueChangedListener(listener) {
        this.valueChangedListeners.push(listener);
    }

    getJSONValue() {
        if (this.valueType === ComponentValueTypes.Number) {
            return this.value;
        } else if (this.valueType === ComponentValueTypes.String) {
            return this.value;
        } else if (this.valueType === ComponentValueTypes.Reference) {
            return null;
        } else if (this.valueType === ComponentValueTypes.Bool) {
            console.log("A: " + this.value);
            return (this.value ? 1 : 0);
        } else if (this.valueType === ComponentValueTypes.Any) {
            return this.value;
        } else {
            console.log("Unsupported Port JSON Value Type (" + this.type + ")");
        }
    }
    
    isDefault() {
        if (this.value == null) {
            return true;
        } else if (this.valueType === ComponentValueTypes.Number) {
            return false;
        } else if (this.valueType === ComponentValueTypes.Reference) {
            return true; //Can't export references.
        } else if (this.valueType === ComponentValueTypes.String) {
            return this.value === "";
        } else if (this.valueType === ComponentValueTypes.Bool) {
            return this.value === false;
        } else if (this.valueType === ComponentValueTypes.Any) {
            return true;
        } else {
            console.log("Unknown Port Value Type (" + this.type + ")");
        }
    }
}

export default ComponentPort;


