import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes.js';

class ComponentAddition extends Component {
    constructor() {
        super();

        this.componentName = "addition circuit";
        this.createPort("A", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("B", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("C", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("D", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("E", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("F", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("G", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("H", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("Result", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createAction("Compute", this.compute);
        this.createAction("On Compute", this.onCompute);
    }

    compute(component, action) {
        let result = component.getPort("A").value;
        let lookup = ["B","C","D","E","F","G","H"];

        if (result != null) {
            for (let i=0; i<lookup.length; i++) {
                let port = component.getPort(lookup[i]);

                if (port.value != null) {
                    result += port.value;
                }
            }

            component.getPort("Result").setValue(result);
            component.runAction("On Compute");
        }
    }

    onCompute() {

    }
}

export default ComponentAddition;


