import React from 'react';

class ActivatorView extends React.Component {
    constructor(props) {
        super(props);
        let links = [];

        for (let i=0; i<this.props.graph.actionLinks.length; i++) {
            let link = this.props.graph.actionLinks[i];

            if (link.action1 === this.props.action || link.action2 === this.props.action) {
                links.push(link);
            }
        }

        this.props.graph.addNewActionLinkCallback(() => this.linkListChanged());
        this.props.graph.addRemovedActionLinkCallback(() => this.linkListChanged());

        this.state = {
            links: links
        }
    }

    render() {
        let links = [];

        for (let i=0; i<this.state.links.length; i++) {
            let actionLink = this.state.links[i];

            if (actionLink.action1 === this.props.action) {
                links.push(
                    <span key={Math.random()}><button onClick={() => this.removeLink(actionLink)}><font color="#FF0000">{actionLink.action2.name}</font></button><font color="#FF0000"> @ {actionLink.action2.component.componentName}</font></span>
                );
                links.push(<br key={Math.random()} />);
            } else { //link.action2 === this.props.action
                links.push(
                    <span key={Math.random()}><button onClick={() => this.removeLink(actionLink)}><font color="#FF0000">{actionLink.action1.name}</font></button><font color="#FF0000"> @ {actionLink.action1.component.componentName}</font></span>
                );
                links.push(<br key={Math.random()} />);
            }
        }

        return (
            <tr key={Math.random()}>
                <td colSpan={3} align="center">
                    <button onClick={() => this.props.actionLinkClicked(this.props.action)}><font color="#FF0000">{this.props.action.name}</font></button><button onClick={() => this.runAction()}>Pulse</button>
                    <br />
                    {links}
                </td>
            </tr>
        )
    }

    runAction() {
        this.props.action.act();
    }

    removeLink(link) {
        this.props.graph.unlinkActions(link);
    }

    linkListChanged() {
        let links = [];

        for (let i=0; i<this.props.graph.actionLinks.length; i++) {
            let link = this.props.graph.actionLinks[i];

            if (link.action1 === this.props.action || link.action2 === this.props.action) {
                links.push(link);
            }
        }

        this.setState({
            links: links
        });
    }
}

export default ActivatorView;


