import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes.js';

class ComponentNum2Text extends Component {
    constructor() {
        super();

        this.componentName = "number to string";
        this.createPort("Input", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("Output", ComponentPortTypes.Out, ComponentValueTypes.String);
        this.createAction("Convert", this.convert);
        this.createAction("On Converted", this.onConverted);
    }

    convert(component, action) {
        if (component.getPort("Input").value != null) {
            component.getPort("Output").setValue(component.getPort("Input").value.toString());
        } else {
            component.getPort("Output").setValue("");
        }
        
        component.runAction("On Converted");
    }

    onConverted(component, action) {
        
    }
}

export default ComponentNum2Text;


