import React from 'react';
import PortView from './PortView.js';
import ActivatorView from './ActivatorView.js';

class ComponentView extends React.Component {
    render() {
        let row_height = Math.max(this.props.component.getInputs().length, this.props.component.getOutputs().length, 1);
        let column_width = 3;
        let inputs = this.props.component.getInputs();
        let outputs = this.props.component.getOutputs();
        let actions = this.props.component.actions;
        let rows = [];
        let activators = [];

        for (let i=0; i<row_height; i++) {
            let columns = [];

            for (let j=0; j<column_width; j++) {
                let words = [];
                let height = 1;

                if (j === 0 && inputs[i] != null) { //Inputs
                    words.push(<PortView key={Math.random()} port={inputs[i]} graph={this.props.graph} portLinkClicked={this.props.portLinkClicked} />);
                } else if (j === 1) { //Description
                    if (i === 0) {
                        words.push(<p key={Math.random()}>Description</p>);
                        height = row_height;
                    } else {
                        continue;
                    }
                } else if (j === 2 && outputs[i] != null) { //Outputs
                    words.push(<PortView key={Math.random()} port={outputs[i]} graph={this.props.graph} portLinkClicked={this.props.portLinkClicked} />);
                }

                columns.push(<td key={Math.random()} align="center" rowSpan={height}>{words}</td>);
            }
            rows.push(<tr key={Math.random()}>{columns}</tr>);
        }
        
        for (let i=0; i<actions.length; i++) {
            let action = actions[i];

            activators.push((
                <ActivatorView key={Math.random()} action={action} graph={this.props.graph} actionLinkClicked={this.props.actionLinkClicked}/>
            ));
        }

        return (
            <div align="center" className="componentView">
                <b><p align="center">{this.props.component.componentName}</p></b>
                <button onClick={() => this.removeComponent()}>Remove</button>
                <table border="1">
                    <colgroup>
                        <col style={{width: "35%"}} />
                        <col style={{width: "30%"}} />
                        <col style={{width: "35%"}} />
                    </colgroup>
                    <tbody>
                        {rows}
                        {activators}
                    </tbody>
                </table>
            </div>
        );
    }

    removeComponent() {
        this.props.graph.removeComponent(this.props.component);
    }
}

export default ComponentView;


