import React from 'react';
import ModifyPortValueView from './ModifyPortValueView.js';
import {ComponentPortTypes, getValueTypeName} from '../Components/ComponentTypes.js';

class PortView extends React.Component {
    constructor(props) {
        super(props);
        let links = [];

        for (let i=0; i<this.props.graph.portLinks.length; i++) {
            let portLink = this.props.graph.portLinks[i];

            if (portLink.inPort === this.props.port || portLink.outPort === this.props.port) {
                links.push(portLink)
            }
        }

        this.props.graph.addNewPortLinkCallback(() => this.linkListChanged());
        this.props.graph.addRemovedPortLinkCallback(() => this.linkListChanged());
        this.props.port.addValueChangedListener((port, value) => this.valueChanged(value));

        this.state = {
            value: this.props.port.value,
            links: links
        };
    }

    render() {
        let links = [];

        for (let i=0; i<this.state.links.length; i++) {
            let portLink = this.state.links[i];

            if (this.props.port.type === ComponentPortTypes.In && portLink.inPort === this.props.port) {
                links.push(<span key={Math.random()}><button onClick={() => this.removeLink(portLink)}>{portLink.outPort.name}</button> @ {portLink.outPort.component.componentName}</span>);
                links.push(<br key={Math.random()} />);
            } else if (this.props.port.type === ComponentPortTypes.Out && portLink.outPort === this.props.port) {
                links.push(<span key={Math.random()}><button onClick={() => this.removeLink(portLink)}>{portLink.inPort.name}</button> @ {portLink.inPort.component.componentName}</span>);
                links.push(<br key={Math.random()} />);
            }
        }

        return (
            <div>
                <p>{"<" + getValueTypeName(this.props.port.valueType) + ">"} <button onClick={() => this.props.portLinkClicked(this.props.port)}>{this.props.port.name}</button> (<button onClick={() => this.showModifyValueView()}>{this.props.port.value == null ? "NULL" : this.props.port.value.toString()}</button>)</p>
                <ModifyPortValueView ref={ref => this.modifyValueView = ref} port={this.props.port}/>
                {links}
            </div>
        )
    }

    valueChanged(value) {
        this.setState({
            value: value
        });
    }

    showModifyValueView() {
        this.modifyValueView.show();
    }
    
    linkListChanged() {
        let links = [];

        for (let i=0; i<this.props.graph.portLinks.length; i++) {
            let portLink = this.props.graph.portLinks[i];

            if (portLink.inPort === this.props.port || portLink.outPort === this.props.port) {
                links.push(portLink)
            }
        }

        this.setState({
            links: links
        })
    }

    removeLink(link) {
        this.props.graph.unlinkPorts(link);
    }
}

export default PortView;


