import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes.js';

class ComponentSign extends Component {
    constructor() {
        super();

        this.componentName = "sign circuit";
        this.createPort("A", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("Result", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createAction("Compute", this.compute);
        this.createAction("On Compute", this.onCompute);
    }

    compute(component, action) {
        if (component.getPort("A").value != null) {
            let result = Math.sign(component.getPort("A").value);

            component.getPort("Result").setValue(result);
            component.runAction("On Compute");
        }
    }

    onCompute() {

    }
}

export default ComponentSign;


