import {ComponentPortTypes, ComponentValueTypes} from './ComponentTypes.js';

class ComponentPortLink {
    constructor(outPort, inPort) {
        if (outPort.type !== ComponentPortTypes.Out || inPort.type !== ComponentPortTypes.In || (outPort.valueType !== inPort.valueType && outPort.valueType !== ComponentValueTypes.Any && inPort.valueType !== ComponentValueTypes.Any)) {
            console.log("Invalid port link (" + outPort.name + "<" + outPort.getTypeName() + "(" + outPort.valueType + ")> to " + inPort.name + "<" + inPort.getTypeName() + "(" + inPort.valueType + ")>)");
            this.valid = false;
            return;
        }

        this.outPort = outPort;
        this.inPort = inPort;
        this.valid = true;

        this.outPort.addValueChangedListener((port, value) => this.outPortValueChanged(port, value));
    }

    outPortValueChanged(port, value) {
        this.inPort.setValue(value);
    }
}

export default ComponentPortLink;


