class ComponentAction {
    constructor(name, component, onAct) {
        this.name = name;
        this.component = component;
        this.actCallbacks = [onAct];
    }

    addActCallback(actCallback) {
        this.actCallbacks.push(actCallback);
    }

    act() {
        for (var i=0; i<this.actCallbacks.length; i++) {
            this.actCallbacks[i](this.component, this);
        }
    }
}

export default ComponentAction;


