import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentSmoke extends Component {
    constructor() {
        super();

        this.componentName = "smoke generator";
        this.createPort("Volume Used", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createPort("Self Reference", ComponentPortTypes.Out, ComponentValueTypes.Reference);
        this.createAction("Create Smoke", this.createSmoke);
        this.createAction("On Smoked", this.onSmoked);
    }

    createSmoke(component, action) {
        component.getAction("On Smoked").act();
    }

    onSmoked(component, action) {

    }
}

export default ComponentSmoke;


